.row {
  display: table;
  width: 100%;
  margin-left: auto;
  margin-right: auto;
}

.row_2 {
  display: table;
  width: 100%;
  margin-top: 3rem;
}

.column_1 {
  display: table-cell;
  width: 4rem;
  vertical-align: top;
}

.column_2 {
  display: table-cell;
  width: 100%;
  vertical-align: top;
}

.column_left {
  display: table-cell;
  text-align: center;
  vertical-align: top;
  height: 100%;
  width: 50vw;
  background-color: transparent;
  overflow: hidden;
  padding-left: 12.5vw;
  padding-right: 10vw;
}

.column_right {
  display: table-cell;
  text-align: center;
  height: 100%;
  width: 50vw;
  vertical-align: top;
  background-color: transparent;
  padding-right: 12.5vw;
}

.column_right_for_mobile {
  display: table-cell;
  text-align: center;
  height: 100%;
  width: 50vw;
  vertical-align: top;
  background-color: whitesmoke;
  margin-right: auto;
}

.card {
  /* display: table; */
  /* table-layout: fixed; */
  -webkit-box-shadow: 0px 0px 0.5rem 0px rgba(143, 143, 143, 1);
  -moz-box-shadow: 0px 0px 0.5rem 0px rgba(143, 143, 143, 1);
  box-shadow: 0px 0px 0.5rem 0px rgba(143, 143, 143, 1);
  width: 75vw;
  max-width: 28rem;
  min-height: 28rem;
  border-radius: 2rem;
  text-align: center;
  vertical-align: top;
  background-color: white;
  margin-left: auto;
  margin-right: auto;
  margin-top: 9rem;
  padding: 2rem;
  margin-bottom: 1rem;
}

/* .card input { */
.textfield {
  outline-width: 0rem;
  border-radius: 1rem;
  height: 3rem;
  width: 90%;
  font-size: 1.2rem;
  padding-left: 1rem;
  padding-right: 1rem;
  border-color: transparent;
  border-style: solid;
  background-color: whitesmoke;
}

.button_2 {
  outline-width: 0rem;
  background-color: #007bfc;
  color: white;
  width: 100%;
  border-radius: 1rem;
  height: 4rem;
  margin-top: 6.2rem;
  border: none;
  font-size: 1.5rem;
  cursor: pointer;
}

.button_2:hover {
  -webkit-box-shadow: 0px 0px 0.5rem 0px rgba(143, 143, 143, 1);
  -moz-box-shadow: 0px 0px 0.5rem 0px rgba(143, 143, 143, 1);
  box-shadow: 0px 0px 0.5rem 0px rgba(143, 143, 143, 1);
}

.button_2:active {
  -webkit-box-shadow: 0px 0px 0px 0px rgba(143, 143, 143, 1);
  -moz-box-shadow: 0px 0px 0px 0px rgba(143, 143, 143, 1);
  box-shadow: 0px 0px 0px 0px rgba(143, 143, 143, 1);
}

.button_3 {
  outline-width: 0rem;
  background-color: white;
  color: #007bfc;
  border-radius: 1rem;
  height: 4rem;
  width: 100%;
  margin-top: 1rem;
  border: none;
  font-size: 1.5rem;
  cursor: pointer;
}

.button_3:hover {
  background-color: whitesmoke;
}

