.message {
  max-width: 30rem;
  margin-top: 20vh;
  margin-left: auto;
  margin-right: auto;
  text-align: center;
}

.message span {
  font-size: 5rem;
}

.message h1 {
  font-size: 2rem;
  font-weight: 600;
  color: rgba(0,0,0,0.6);
  text-align: center;
}

.message p {
  margin: 1rem 0 1.5rem;
  font-size: 1.5rem;
  color: rgba(0,0,0,0.6);
  text-align: center;
}

.button {
  outline-width: 0rem;
  background-color: #007BFC;
  border-radius: 1rem;
  color: white;
  text-decoration: none;
  border: none;
  border-style: solid;
  border-color: transparent;
  text-align: center;
  width: 20rem;
  padding-top: 1.2rem;
  padding-bottom: 1.2rem;
  font-size: 1.5rem;
  cursor: pointer;
}

.button:hover {
  -webkit-box-shadow: 0px 0px 0.5rem 0px rgba(143,143,143,1);
  -moz-box-shadow: 0px 0px 0.5rem 0px rgba(143,143,143,1);
  box-shadow: 0px 0px 0.5rem 0px rgba(143,143,143,1);
}

p {
  color:  rgba(0,0,0,0.6);
  text-align: center;
  margin-top: 2rem;
  width: 100%;
}

span {
  font-size: 4rem;
}